/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lexend", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.section {
  padding: 2rem 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

a {
  text-decoration: none;
}

[class$=__heading] {
  margin-block: 2rem;
}

.header {
  padding: 2rem;
  position: relative;
}
.header__heading {
  margin: 0;
  text-align: center;
}
.header__heading::before {
  content: "";
  display: block;
  width: 15rem;
  height: 15rem;
  background-image: url(../img/alex.jpg);
  background-size: cover;
  background-position: center center;
  margin: 0 auto 2rem auto;
}
.header__links {
  font-size: 5rem;
  text-align: center;
  margin-top: 4rem;
  line-height: 0;
}
@media (min-width: 768px) {
  .header__links {
    position: absolute;
    right: 5rem;
    top: 0rem;
  }
}

.projects__heading {
  font-size: 3.6rem;
  display: inline-block;
  color: #000;
}
.projects__heading::after {
  content: "";
  background-color: #6EA4BF;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.9rem 0 0 0;
}
.projects__list {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 1024px) {
  .projects__list {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.project {
  background-color: #FFFFFF;
  box-shadow: 0px 0px 15px 3px rgba(0, 0, 0, 0.1);
  align-items: center;
  padding: 1rem;
}
@media (min-width: 480px) {
  .project {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.project__img-link {
  border: 1px solid #e9e9e9;
}
@media (min-width: 480px) {
  .project__img-link {
    display: inline-block;
  }
}
.project__img {
  width: 30rem;
  margin: 0 auto;
}
.project__links {
  display: flex;
}
.project__name, .project__description {
  text-align: center;
  margin: 0 0 1rem 0;
}
@media (min-width: 480px) {
  .project__name, .project__description {
    text-align: left;
  }
}
.project__link {
  background-color: #FFFFFF;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #7f07d8;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  display: block;
}
.project__link:hover {
  cursor: pointer;
  background-color: #e6e6e6;
}
@media (min-width: 768px) {
  .project__link {
    width: auto;
    display: inline-block;
  }
}
@media (min-width: 768px) {
  .project__link {
    display: block;
  }
}